import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  CustomSelect,
  CustomDatePicker,
} from '../components/Filters';
import {
  SELECT_TYPE_LABEL,
  LEASE_TERM_OPTIONS,
} from '../constants/Filters/SelectOptionValue';
import PricingDetailsCard from '../components/Cards/PricingDetailsCard';
import LeaseTermHeader from '../components/LeaseTerm/LeaseTermHeader';
import PricingCalendarButton from '../components/LeaseTerm/PricingCalendarButton';
import { useNavigate } from 'react-router-dom';
import '../App.css';
import { setLeaseTerm } from '../redux/slices/userSelectionSlice';
import { setTerm, setMoveInDate } from '../redux/slices/searchFilterSlice';
import { RouteTypes } from '../constants/Routes/RouteTypes';
import PricingCalendarModal from '../components/Modals/PricingCalendarModal';
import { format, parseISO } from 'date-fns';
import Query from '../services/utils/Query';
import { loadUnitPricing } from '../services/ApartmentQuery/apartmentQueries';
import Spinner from "../components/Common/Spinner";
import NoResultsFound from "../components/Common/NoResultsFound";
import BackButton from "../components/Common/BackButton";
import { Screens } from '../constants/Screens/Screens';

function Pricing() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const configuration = useSelector((state) => { return state.configuration; });
  const apartment = useSelector((state) => { return state.userSelection.apartment; });
  const floorPlan = useSelector((state) => { return state.userSelection.floorPlan; });
  const selection = useSelector((state) => { return state.userSelection; });
  const filters = useSelector((state) => { return state.searchFilter; });

  const [showPricingCalendarModal, setShowPricingCalendarModal] = useState(false);
  const [apartments, setApartments] = useState([]);
  const [leaseTerm, setLeaseTerm] = useState([]);
  const [pricingMoveInDate, setPricingMoveInDate] = useState(filters.moveInDate || '2022-01-11');
  const [pricingTerm, setPricingTerm] = useState('');
  const [apartmentResults, setApartmentResults] = useState([]);
  const [pricingResults, setPricingResults] = useState([]);
  const [loading, setLoading] = useState(true);

  const handleOnchange = (event) => {
    console.log(event.target.value);
  };

  const handleSelectLeaseTerm = (data) => {
    dispatch(setLeaseTerm(data));
    navigate(RouteTypes.ITEMS);
  };

  const viewPricingCalendar = () => {
    setShowPricingCalendarModal(!showPricingCalendarModal);
  };

  const closeViewImage = () => {
    setShowPricingCalendarModal(!showPricingCalendarModal);
  };

  const getUnitPricing = async() => {
      setLoading(true);
      const moveDate = format(parseISO(pricingMoveInDate), "yyyy-MM-dd'T'HH:mm:ss.SSS+0000"); // +0000 instead of XX getting actual timezone
      console.log("Move-in Date: " + moveDate);
      let body = {
        'moveInDate': moveDate
      };
      console.log(pricingTerm);
      if (pricingTerm != '') body.term = pricingTerm;
  
      try {
        console.log(apartment);

        let { records } = await loadUnitPricing(apartment.id, body);

        const pricingResults = records;
        setPricingResults(pricingResults);

      } catch (error) {
        console.log(JSON.stringify(error));
      } finally {
        setLoading(false);
      }
  };


  // init onload
  useEffect(() => {
     // refresh browser handling
     if (Object.keys(selection.floorPlan).length === 0) {
      navigate(RouteTypes.FLOORPLANS);
    }
    dispatch(setMoveInDate(pricingMoveInDate));
    dispatch(setTerm(pricingTerm));
    

    const delayDebounceFn = setTimeout(() => {
      getUnitPricing();
    }, 1000);
    return () => clearTimeout(delayDebounceFn);
  }, [pricingMoveInDate, pricingTerm]);

  return (
    <div id="LeaseTerm">
      <LeaseTermHeader floorplan={floorPlan} apartment={apartment} />
      <div className="row">
        <CustomDatePicker onChange={setPricingMoveInDate} value={pricingMoveInDate} />
        <CustomSelect label={SELECT_TYPE_LABEL.LEASETERM} options={LEASE_TERM_OPTIONS} onChange={setPricingTerm} value={pricingTerm}/>
        <PricingCalendarButton viewPricingCalendar={viewPricingCalendar}/>
      </div>
      <div>
        {!loading ?
          pricingResults.length > 0 ? pricingResults.map((leaseterm, index) => {
            console.log(pricingResults.length);
            return <div key={index}>
              <PricingDetailsCard
                data={leaseterm}
                moveInDate={format(parseISO(pricingMoveInDate), "MM/dd/yyyy")}
                select={handleSelectLeaseTerm}>
              </PricingDetailsCard>
            </div>;
          })
          : <NoResultsFound label={Screens.LEASETERMS}/>
        : <Spinner /> }
      </div>
      { !loading ?
        <BackButton route={RouteTypes.APARTMENTS} /> : ''
      }
      {
        showPricingCalendarModal === true && (
          <PricingCalendarModal closeModal={closeViewImage}/>
        )
      }
      
    </div>
    
  );
}

export default Pricing;
