export const loadUnits = async (floorPlanId) => {
  try {
    const {data} = await coreApi.post(`${apiData.urlUnits}/${floorPlanId}?page=${page}&recordsPerPage=${recordPerPage}`, '');
    const {records} = data;
    return {records};
  } catch (error) {
    if (error.response) {
      throw new Error(error.response);
    } else {
      throw new Error(`API ERROR${  error}`);
    }
  }
};

export const loadUnitPricing = async (unitId, body) => {
  try {
    const {data} = await coreApi.post(`${apiData.urlUnitPricing}/${unitId}?page=${page}&recordsPerPage=${recordPerPage}`, body);
    const {records} = data;
    return {records};
  } catch (error) {
    if (error.response) {
      throw new Error(error.response);
    } else {
      throw new Error(`API ERROR${  error}`);
    }
  }
};
