import { createSlice } from '@reduxjs/toolkit';

const initialUserSelectionState = {
  floorPlan: {},
  apartment: {},
  rentableItems: {},
  leaseTerm: {},
};

export const userSelectionSlice = createSlice({
  name: 'userSelection',
  initialState: initialUserSelectionState,
  reducers: {
    setFloorPlan(state, action) {
      state.floorPlan = action.payload;
    },
    setApartment(state, action) {
      state.apartment = action.payload;
    },
    setRentableItems(state, action) {
      state.rentableItems = action.payload;
    },
    setLeaseTerm(state, action) {
      state.leaseTerm = action.payload;
    },
  },
});

export const { setFloorPlan, setApartment, setRentableItems, setLeaseTerm } = userSelectionSlice.actions;

export default userSelectionSlice.reducer;
