import React from 'react';
import CustomInput from '../Filters/CustomInput';
import CustomDatePicker from '../Filters/CustomDatePicker';

const ApartmentFilter = ({filterProps}) => {
  return (
    <div className="row">
      <CustomDatePicker label={'Desired Move-In Date'}
        onChange={filterProps.setMoveInDate}
        value={filterProps.moveInDate}
      />
      <CustomInput label={'Apartment Number'}
        onBlur={filterProps.setApartmentNumber}
        value={filterProps.apartmentNumber}
        type={'name'}
      />
    </div>
  );
};

export default ApartmentFilter;

