import React from 'react';

const CustomDatePicker = ({ label, onBlur, value, type, ref }) => {
  return (
    <div className='form-group col-6 col-sm-6 col-md'>
      <label>{label}</label>
      <input
        ref={ref}
        type='text'
        value={value}
        className='form-control'
        onChange={(event) => onBlur(event.target.value, type)}
      />
    </div>
  );
};

export default CustomDatePicker;
