import React from 'react';
import { makeStyles, createStyles } from '@material-ui/styles';
import { Constants } from '../../constants/FloorPlan/FloorPlan';

const useStyle = makeStyles(() => {
  return createStyles({
    layoutFloorPlanImage: {
      height: '200px',
      marginBottom: '24px',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center',
      backgroundSize: 'cover',
    },
  });
});

const FloorPlanCards = ({ data, viewImage, applyNow }) => {
  const classes = useStyle();
  const { bathrooms, bedrooms, name, livingArea, livingAreaMeasure, media } = data;
  const { downloadUrl } = media[0];
  return (
    <div class='col-lg-4 col-md-4 col-sm-12 col-xs-12'>
      <div class='container content_center'>
        <div
          style={{
            cursor: 'pointer',
          }}
          className={classes.layoutFloorPlanImage}
          onClick={() =>
            viewImage(
              bathrooms,
              bedrooms,
              name,
              livingArea,
              livingAreaMeasure,
              downloadUrl
            )
          }
        >
          <img alt={Constants.NO_IMAGE} src={downloadUrl} />
        </div>
        <h4>{name}</h4>
        <p>
          {bedrooms} Bedroom | {bathrooms} Bath | {livingArea}
          {livingAreaMeasure}
        </p>

        <div class='layout_button_group_stacked'>
          <button
            className="target"
            type="button"
            button="primary"
            onClick={() => applyNow(data)}
          >
            {Constants.APPLY_NOW}
          </button>
        </div>
      </div>
    </div>
  );
};

export default FloorPlanCards;
