import React from 'react';
import { Provider } from 'react-redux';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import _ from 'lodash';
import { RouteTypes } from './constants/Routes/RouteTypes';
import FloorPlan from './screens/FloorPlan';
import Apartment from './screens/Apartment';
import Pricing from './screens/Pricing';
import Item from './screens/Item';
import { store } from './redux/store';
import config from './config.json';
import './App.css';

function App(props) {
  let configuration = config;
  console.log(JSON.stringify(configuration));
  if(!_.isEmpty(props)){
    configuration = props;
  }
  return (
    <div className="App">
      <Provider store={store}>
        <BrowserRouter>
          <Routes>
            <Route path={RouteTypes.FLOORPLANS} element={<FloorPlan configuration={configuration} />} />
            <Route path={RouteTypes.APARTMENTS} element={<Apartment />} />
            <Route path={RouteTypes.PRICING} element={<Pricing />} />
            <Route path={RouteTypes.ITEMS} element={<Item />} />
          </Routes>
        </BrowserRouter>
      </Provider>

    </div>
  );
}

export default App;
