import React, { useState, useRef } from 'react';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import '../App.css';
import '../components/Cards/Apartment.css';
import {
  CustomSelect,
  CustomDatePicker,
  CustomInput,
  CustomInputRange,
  CustomRangeFilter
} from '../components/Filters';
import {
  BEDROOMS_OPTIONS,
  BATHROOMS_OPTIONS,
  DENS_OPTIONS,
  AREA_RANGE_VAL,
  RENT_RANGE,
  SELECT_TYPE_LABEL,
} from '../constants/Filters/SelectOptionValue';
import Query from '../services/utils/Query';
import { setConfiguration } from '../redux/slices/configurationSlice';
import { loadFloorPlan, loadFloorPlanApartment } from '../services/FloorplanQuery/floorPlanQueries';
import { RouteTypes } from '../constants/Routes/RouteTypes';
import { setFloorPlan } from '../redux/slices/userSelectionSlice';
import { setApartmentNumber, setBathrooms, setBedrooms, setDen } from '../redux/slices/searchFilterSlice';
import { FloorPlanCards } from '../components/Cards';
import FloorPlanImageModal from '../components/Modals/FloorPlanImageModal';
import AparmentCards from '../components/Cards/AparmentCards';
import Spinner from "../components/Common/Spinner";
import NoResultsFound from "../components/Common/NoResultsFound";

function FloorPlan(configuration) {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [selectedDate, setSelectedDate] = React.useState('');
  const [selectedApartment, seSelectApartment] = React.useState('');
  const [showImageModal, setShowImageModal] = useState(false);
  const [selectedImage, setSelectedImage] = useState({});
  const [loading, setLoading] = useState(false)

  const handleOnchange = async (val, field) => {
    const query = new Query();
    query.join(null, 'unit', 'id', 'floorPlanId', 'u');
    if (field === 'bedrooms' && val) {
      dispatch(setBedrooms(val));
      console.log(val);
      if (val === '4+') {
        query.and(`${field}`).greaterThanOrEq(Number(4));
      } else {
        query.and(`${field}`).eq(Number(val));
      }
    }
    if (field === 'bathrooms' && val) {
      dispatch(setBathrooms(val));
      console.log(val);
      if (val === '3+') {
        query.and(`${field}`).greaterThanOrEq(Number(3));
      } else {
        query.and(`${field}`).eq(Number(val));
      }
    }
    if (field === 'dens' && val) {
      dispatch(setDen(val));
      console.log(val);
      query.and(`${field}`).eq(Number(val));
    }
 
    try {
     setLoading(true);
      const { records } = await loadFloorPlan(query);
      setFloorPlanList(records);
      console.log(records);
    } catch (error) {
      console.log(JSON.stringify(error));
    } finally {
      setLoading(false);
    }
  };

  const inputRef = useRef();
  const throttling = useRef(false);
  const handleSearch = (val, field) => {
    setLoading(true);
    dispatch(setApartmentNumber(val));
    seSelectApartment(val);
    if (throttling.current) {
      return;
    }

    throttling.current = true;
    setTimeout(async () => {
      throttling.current = false;
      const query = new Query();
      query.join(null, 'floor_plan', 'floorPlanId', 'id', 'u', 'bedrooms', 'bathrooms', 'name', 'livingAreaMeasure', 'livingArea');
      query.and('unitNumber').match(val);

      try {
        const { records } = await loadFloorPlanApartment(query);
        setFloorPlanApartmentList(records);
        console.log(records);
      } catch (error) {
        console.log(JSON.stringify(error));
      } finally {
         setLoading(false);
      }
    }, 600);
  };

  const [UnitSizeMin, setUnitSizeMin] = React.useState('');
  const [UnitSizeMax, setUnitSizeMax] = React.useState('');

  const datePick = (date) => {
    setSelectedDate(date);
    console.log(date);
  };

  const showDrpdown = (containerID) => {
    const unitSizeContainer = document.querySelector(`#${containerID}`);
    unitSizeContainer.style.display = 'block';
  };

  const [floorPlanList, setFloorPlanList] = useState([]);
  const [floorPlanListApartment, setFloorPlanApartmentList] = useState([]);

  React.useEffect(() => {
    dispatch(setConfiguration(configuration));
 setLoading(true);
    async function getFloorplan() {
      const query = new Query();

      try {
        const { records } = await loadFloorPlan(query);
        setFloorPlanList(records);
        console.log(records);
      } catch (error) {
        console.log(JSON.stringify(error));
      } finally {
       setLoading(false);
      }
    }
    getFloorplan();
  }, []);

  const handleOnClick = async (data) => {
    await dispatch(setFloorPlan(data));
    await navigate(RouteTypes.APARTMENTS);
  };

  const viewImage = (
    bathrooms,
    bedrooms,
    name,
    livingArea,
    livingAreaMeasure,
    downloadUrl
  ) => {
    setSelectedImage({
      bathrooms,
      bedrooms,
      name,
      livingArea,
      livingAreaMeasure,
      downloadUrl,
    });
    setShowImageModal(!showImageModal);
  };

  const closeViewImage = () => {
    setShowImageModal(!showImageModal);
  };
  

async function filterallfields(fields) {
  // updateQueryString(fieldId, field);
  console.log(fields);
  let query = new Query();
  //  query.join(null, 'unit', 'id', 'floorPlanId', 'u');
  fields.forEach((field) => {
    if (field.id === 'bedrooms' && field.value) {
      console.log(field.value);
      if (field.value === '4+') {
        query.and(`${field.id}`).greaterThanOrEq(Number(4));
      } else {
        query.and(`${field.id}`).eq(Number(field.value));
      }
    }

    if (field.id === 'bathrooms' && field.value) {
      if (field.value === '3+') {
        query.and(`${field.id}`).greaterThanOrEq(Number(4));
      } else {
        query.and(`${field.id}`).eq(Number(field.value));
      }
    }

    if (field.id === 'dens' && field.value) {
      query.and(`${field.id}`).eq(Number(field.value));
    }

       if (field.id === 'area_range' && field.value) {
        const val = field.value;
        let myArr = val.split("-")
        let min = myArr[0]
        let max = myArr[1]
        console.log(max)
    }
     if (field.id === 'rent_range' && field.value) {
        const val = field.value;
        let myArr = val.split("-")
        let min = myArr[0]
        let max = myArr[1]
        console.log(max)
    }
  });
  console.log(query);
   try {
      let { records } = await loadFloorPlan(query);
      setFloorPlanList(records);
      console.log(records);
    } catch (error) {
      console.log(JSON.stringify(error));
    } finally {
      // setLoading(false);
    }
  // getFloorPlanList(q);
}

  let textFields = document.querySelectorAll('select');
let index = 0;
textFields.forEach((textField) => {
  if (textField.localName === 'select' || textField.id === 'date') {
    textField.onchange = function () {
      filterallfields(textFields);
    };
  } else {
    textField.onkeyup = function () {
      // filterallfields(textFields);
    };
  }
});

  return (
    <div>
      <div className='row'>
          <CustomDatePicker onChange={datePick} value={selectedDate} />
        <CustomSelect
          label={SELECT_TYPE_LABEL.BEDROOMS}
          onChange={handleOnchange}
          options={BEDROOMS_OPTIONS}
          type={'bedrooms'}
        />
        <CustomSelect
          label={SELECT_TYPE_LABEL.BATHROOMS}
          onChange={handleOnchange}
          options={BATHROOMS_OPTIONS}
          type={'bathrooms'}
        />
        </div>
        <div className="row">
        <CustomSelect
          label={SELECT_TYPE_LABEL.DENS}
          onChange={handleOnchange}
          options={DENS_OPTIONS}
          type={'dens'}
        />
        {/* <CustomInputRange
          containerID={'unit-size-container'}
          label={'Unit Size'}
          onClickShow={() => showDrpdown('unit-size-container')}
          symbol={'Sq.Ft.'}
          minValue={UnitSizeMin}
          maxValue={UnitSizeMax}
          onChangeMin={setUnitSizeMin}
          onChangeMax={setUnitSizeMax}
        /> */}
          <CustomRangeFilter
        label={SELECT_TYPE_LABEL.RENT_RANGE}
        options={RENT_RANGE}
        type="rent_range"
 onChange={handleOnchange}
 symbol="$"
        />

        <CustomRangeFilter
        label={SELECT_TYPE_LABEL.AREA_RANGE}
        options={AREA_RANGE_VAL}
        type="area_range"
 onChange={handleOnchange}
 symbol="sqft"
        />

        
      
        <CustomInput
          label={'Apartment #'}
          onBlur={handleSearch}
          value={selectedApartment}
          type={'name'}
          ref={inputRef}
        />
      </div>

  

      <div className='row'>
        {loading ? <Spinner/> : selectedApartment.length > 0 && floorPlanListApartment.length > 0
          ?  <AparmentCards data={floorPlanListApartment} />
          : floorPlanList.length > 0 ? (
            floorPlanList.map((item, i) => {
              return <FloorPlanCards data={item} viewImage={viewImage} applyNow={handleOnClick} key={i} />;
            })
          ) : (
            <div>

              <NoResultsFound label={RouteTypes.FLOORPLANS}/>
            
            </div>
          )}
      </div>
      
      {
        showImageModal == true && (
          <FloorPlanImageModal closeModal={closeViewImage} data={selectedImage} />
        )
      }
    </div >
  );
}
export default FloorPlan;
