import React from 'react';

const CustomSelect = ({ label, options, onChange, type }) => {
  return (
    <div className="form-group col-6 col-sm-6 col-md">
      <label>{label}</label>
      <select id={type} >
          <option value="" selected >All</option>
        {options &&
          options.map((item, i) => {
            const { label, value } = item;
            return (
              <option key={i} value={`${value}`}>
                {label}
              </option>
            );
          })}
      </select>
    </div>
  );
};

export default CustomSelect;
