e: 12px;\n}\n\n/* ------------------------Defaults---------------------------------- */\n\n* {\n  padding: 0px;\n  margin: 0px;\n  box-sizing: border-box;\n  outline: none;\n  font-weight: 300;\n  font-size: var(--body-text-size);\n  font-style: normal;\n  -webkit-appearance: none;\n  -moz-appearance: none;\n  appearance: none;\n  -webkit-font-smoothing: antialiased;\n  font-family: sans-serif;\n  line-height: 1.2em;\n  color: var(--text-color);\n}\n\nbody,\nhtml {\n  height: 100%;\n  padding: 40px;\n}\n\n/* ------------------------Typography---------------------------------- */\n\nh1,\nh2,\nh3,\nh4 {\n  clear: both;\n  font-weight: 600;\n}\n\nh1 {\n  font-size: 26px;\n}\n\nh2 {\n  font-size: 24px;\n}\n\nh3 {\n  font-size: 18px;\n}\n\nh4 {\n  font-size: 14px;\n}\n\np {\n  font-size: var(--body-text-size);\n  margin-bottom: 8px;\n  line-height: 1.5em;\n}\n\np.caption {\n  font-size: var(--caption-text-size);\n}\n\np.error {\n  color: var(--error-color);\n  font-size: var(--caption-text-size);\n}\n\n.align_center {\n  text-align: center;\n}\n\nh1 + P,\nh2 + P,\nh3 + P,\nh4 + P {\n  margin-top: 8px;\n}\n\n/* -----------------------------------PV---------------------------------- */\n\n#PV_Wrapper {\n  display: flex;\n  flex-direction: column;\n  position: relative;\n  width: 100%;\n  height: auto;\n}\n\n#PV_Header {\n  display: flex;\n  flex-direction: row;\n  position: relative;\n  width: 100%;\n  height: auto;\n  justify-content: space-between;\n}\n\n#PV_Body {\n  padding-top: 32px;\n}\n\n#PV_PageTitle {\n  margin-bottom: 32px;\n}\n\n#PV_PageContent {\n  display: flex;\n  flex-direction: row;\n  position: relative;\n  width: 100%;\n  height: auto;\n}\n\n#PV_PageContent #PV_PageContent_Main {\n  flex-shrink: 1;\n  flex-grow: 1;\n}\n\n#PV_PageContent #PageContent_Right {\n  flex-shrink: 0;\n  flex-grow: 0;\n  width: 300px;\n  margin-left: 40px;\n}\n\n/* ------------------------Form Elements---------------------------------- */\n\nlabel {\n  font-size: 12px;\n  display: inline-block;\n  font-weight: 300;\n  background-color: white;\n  margin-left: 10px;\n  padding: 0px 8px;\n}\n\ninput[type='text'],\nselect,\ntextarea {\n  width: 100%;\n  border-color: var(--mediumgray-color);\n  border-radius: var(--input-border-radius);\n  border-style: solid;\n  border-width: 1px;\n  display: block;\n  padding: 16px 16px;\n  background-color: white;\n  height: 54px;\n  font-weight: 600;\n  margin-bottom: 8px;\n  background-size: 18px;\n  background-position: center right 16px;\n  background-repeat: no-repeat;\n}\n\nselect {\n  cursor: pointer;\n  background-image: url('./assets/img/chevron_down.svg');\n}\n\ninput[type='text'].icon_datepicker {\n  cursor: pointer;\n  background-image: url('./assets/img/calendar.svg');\n}\n\ninput[type='text']:focus,\nselect:focus,\ntextarea:focus {\n  border-color: var(--primary-color);\n}\n\nlabel + input[type='text'],\nlabel + select,\nlabel + textarea {\n  margin-top: -11px;\n}\n\nlabel + input[type='date'],\nlabel + select,\nlabel + textarea {\n  margin-top: -11px;\n}\n\ntextarea {\n  resize: none;\n  height: auto;\n}\n\ninput[type='date'] {\n  width: 100%;\n  border-color: var(--mediumgray-color);\n  border-radius: var(--input-border-radius);\n  border-style: solid;\n  border-width: 1px;\n  display: block;\n  padding: 16px 16px;\n  background-color: white;\n  height: 54px;\n  font-weight: 600;\n  margin-bottom: 8px;\n  background-size: 18px;\n  background-position: center right 16px;\n  background-repeat: no-repeat;\n}\n\ninput[type='text']:disabled,\nselect:disabled {\n  background-color: var(--lightgray-color) !important;\n  border-color: var(--mediumgray-color) !important;\n  color: var(--mediumgray-color) !important;\n}\n\ninput[type='radio'] {\n  width: 18px;\n  height: 18px;\n  border-color: var(--mediumgray-color);\n  border-radius: 100%;\n  border-style: solid;\n  border-width: 1px;\n  cursor: pointer;\n}\n\ninput[type='radio']:checked {\n  border-color: var(--primary-color);\n  border-width: 6px;\n}\n\ninput[type='checkbox'] {\n  width: 18px;\n  height: 18px;\n  border-color: var(--mediumgray-color);\n  border-radius: 2px;\n  border-style: solid;\n  border-width: 1px;\n  cursor: pointer;\n}\n\ninput[type='checkbox']:checked {\n  background-color: var(--primary-color);\n  border-color: var(--primary-color);\n  background-repeat: no-repeat;\n  background-image: url('./assets/img/checkmark_white.svg');\n  background-position: center;\n  background-size: 10px;\n}\n\ninput[type='checkbox']:disabled,\ninput[type='radio']:disabled {\n  border-color: var(--mediumgray-color);\n  background-color: var(--lightgray-color);\n  cursor: not-allowed;\n}\n\ninput.error {\n  border-color: var(--error-color) !important;\n}\n\n/* ------------------------Layouts---------------------------------- */\n\n.layout_select_list_grouping {\n  display: flex;\n  flex-direction: row;\n  align-items: stretch;\n}\n\n.layout_select_list_grouping .select_item {\n  flex-shrink: 0;\n  padding-right: 24px;\n  padding-top: 2px;\n  display: flex;\n}\n\n.layout_select_list_grouping .select_item.vertical_center {\n  align-items: center;\n}\n\n.layout_select_list_grouping .content {\n  flex-grow: 1;\n  flex-direction: column;\n  display: flex;\n}\n\n.layout_select_list_grouping + .layout_select_list_grouping {\n  margin-top: 24px;\n}\n\n.layout_select_list_grouping .content .content_row {\n  display: flex;\n  flex-direction: row;\n  margin-bottom: 24px;\n}\n\n.layout_select_list_grouping .content .content_row:last-of-type {\n  margin-bottom: 0px;\n}\n\n.layout_select_list_grouping .content .content_column {\n  flex-grow: 1;\n  flex-shrink: 1;\n  margin-right: 24px;\n}\n\n.layout_select_list_grouping .content .content_column:last-of-type {\n  margin-right: 0px;\n}\n\n.layout_select_list_grouping .content .content_column.no_shrink {\n  flex-shrink: 0;\n}\n\n.layout_button_group_stacked,\n.layout_button_group_inline {\n  display: flex;\n  margin-top: 24px;\n  justify-content: center;\n}\n\n.layout_button_