import React from 'react';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { RouteTypes } from '../../constants/Routes/RouteTypes';
import { setApartmentNumber, setMoveInDate } from '../../redux/slices/searchFilterSlice';
import { setApartment } from '../../redux/slices/userSelectionSlice';

const ApartmentList = ({ apartments, filterProps }) => {

  const navigate = useNavigate();
  const dispatch = useDispatch();
  return (
    <div className="row">
      {
        apartments.map((apartment) =>
          <div key={apartment.id} className="container">
            <div className="col-xs-2">
              <div className="col-xs-push-1">Apartment</div>
              <div className="col-xs-push-1 detail">{apartment.unitNumber}</div>
            </div>
            <div className="col-xs-2">
              <div className="col-xs-push-1">Area</div>
              <div className="col-xs-push-1 detail">{apartment['floorPlan.livingArea']} {apartment['floorPlan.livingAreaMeasure']}</div>
            </div>
            <div className="col-xs-2">
              <div className="col-xs-push-1">Price per Month</div>
              <div className="col-xs-push-1 detail">
                { apartment.minPrice !== null ? `$${apartment.minPrice}` : '' } - { apartment.maxPrice !== null ? `$${apartment.maxPrice}*` : '' }
              </div>
            </div>
            <div className="col-xs-2"></div>
            <div className="col-xs-4 button">
              <button className="gold target"
                button="primary"
                onClick={async(e) => {
                  await dispatch(setMoveInDate(filterProps.moveInDate));
                  await dispatch(setApartmentNumber(filterProps.apartmentNumber));
                  await dispatch(setApartment(apartment));
                  await navigate(RouteTypes.PRICING);
                }}>
              Select
              </button>
            </div>
          </div>
        )
      }
      <p className="pricing_note">*price subject to change.</p>
    </div>
  );
};

export default ApartmentList;
