import { configureStore } from '@reduxjs/toolkit';
import searchFilterReducer from './slices/searchFilterSlice';
import searchResultReducer from './slices/searchResultSlice';
import userSelectionReducer from './slices/userSelectionSlice';
import configurationReducer from './slices/configurationSlice';


export const store = configureStore({
  reducer: {
    configuration: configurationReducer,
    searchResult: searchResultReducer,
    searchFilter: searchFilterReducer,
    userSelection: userSelectionReducer,
  },
});

export default store;
