import { createSlice } from '@reduxjs/toolkit';

const initialSearchFilterState =  {
  moveInDate: '',
  bedrooms: '',
  bathrooms: '',
  den: '',
  rentRange: '',
  areaRange: '',
  apartmentNumber: '',
  term: '',
};

export const searchFilterSlice = createSlice({
  name: 'searchFilter',
  initialState: initialSearchFilterState,
  reducers: {
    setMoveInDate(state, action) {
      console.log(action.payload);
      state.moveInDate = action.payload;
    },
    setBedrooms(state, action) {
      state.moveInDate = action.payload;
    },
    setBathrooms(state, action) {
      state.bathrooms = action.payload;
    },
    setDen(state, action) {
      state.den = action.payload;
    },
    setRentRange(state, action) {
      state.rentRange = action.payload;
    },
    setAreaRange(state, action) {
      state.areaRange = action.payload;
    },
    setApartmentNumber(state, action) {
      state.apartmentNumber = action.payload;
    },
    setTerm(state, action) {
      state.term = action.payload;
    },
    resetFloorPlanScreen(state) {
      state = initialSearchFilterState;
    },
  },
});

export const { setMoveInDate,
  setBedrooms,
  setBathrooms,
  setDen,
  setRentRange,
  setAreaRange,
  setApartmentNumber,
  setTerm,
  resetFloorPlanScreen,
} = searchFilterSlice.actions;

export default searchFilterSlice.reducer;
